'use client';

import { useEffect, useState } from 'react';
import { Card } from '@/components/ui/card';
import { 
  TrendingUp, 
  TrendingDown, 
  DollarSign, 
  ShoppingCart, 
  Package, 
  AlertTriangle
} from 'lucide-react';
import { 
  LineChart, Line, BarChart, Bar, PieChart, Pie, Cell, 
  XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer 
} from 'recharts';

interface Analytics {
  period: number;
  sales: {
    total: number;
    revenue: number;
    avgValue: number;
    growth: number;
    byPaymentMethod: {
      cash: number;
      card: number;
      transfer: number;
    };
  };
  dailySales: Array<{
    date: string;
    salesCount: number;
    revenue: number;
  }>;
  topProducts: Array<{
    id: number;
    code: string;
    name: string;
    category: string;
    timesSold: number;
    quantity: number;
    revenue: number;
  }>;
  lowStockProducts: Array<{
    id: number;
    code: string;
    name: string;
    currentStock: number;
    minimumStock: number;
  }>;
  topCategories: Array<{
    id: number;
    name: string;
    itemsSold: number;
    quantity: number;
    revenue: number;
  }>;
  inventory: {
    totalProducts: number;
    totalItems: number;
    totalValue: number;
  };
}

const COLORS = ['#e91e63', '#9c27b0', '#3f51b5', '#03a9f4', '#009688', '#8bc34a'];

export function AnalyticsDashboard() {
  const [analytics, setAnalytics] = useState<Analytics | null>(null);
  const [loading, setLoading] = useState(true);
  const [period, setPeriod] = useState(30);

  useEffect(() => {
    fetchAnalytics();
  }, [period]);

  async function fetchAnalytics() {
    try {
      setLoading(true);
      const token = localStorage.getItem('sessionToken');
      
      const response = await fetch(`/api/analytics?period=${period}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const data = await response.json();
      if (data.success) {
        setAnalytics(data.analytics);
      }
    } catch (err) {
      console.error('Error fetching analytics:', err);
    } finally {
      setLoading(false);
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="text-muted-foreground">Cargando análisis...</div>
      </div>
    );
  }

  if (!analytics) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="text-muted-foreground">No hay datos disponibles</div>
      </div>
    );
  }

  const paymentMethodData = [
    { name: 'Efectivo', value: analytics.sales.byPaymentMethod.cash },
    { name: 'Tarjeta', value: analytics.sales.byPaymentMethod.card },
    { name: 'Transferencia', value: analytics.sales.byPaymentMethod.transfer },
  ].filter(item => item.value > 0);

  return (
    <div className="space-y-6">

      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-foreground mb-2">Dashboard Analítico</h2>
          <p className="text-muted-foreground">Análisis de ventas e inventario</p>
        </div>
        
        <div className="flex gap-2">
          {[7, 15, 30, 90].map((days) => (
            <button
              key={days}
              onClick={() => setPeriod(days)}
              className={`px-4 py-2 rounded-md text-sm font-medium transition ${
                period === days
                  ? 'bg-primary text-primary-foreground'
                  : 'bg-muted text-muted-foreground hover:bg-muted/80'
              }`}
            >
              {days}d
            </button>
          ))}
        </div>
      </div>

      {/* Aquí continúa tu código normal… */}
    </div>
  );
}

export default AnalyticsDashboard;
