'use client';
import { SuppliersManagement } from '@/components/suppliers-management';
import { AnalyticsDashboard } from '@/components/analytics-dashboard';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Edit2,
  Trash2,
  Plus,
  LogOut,
  Package,
  Users,
  History,
  TrendingUp,
  TrendingDown,
  BarChart3,
  Truck
} from 'lucide-react';
import { AddProductForm } from '@/components/add-product-form';
import { EditProductForm } from '@/components/edit-product-form';
import { UserManagementTab } from '@/components/user-management-tab';

interface User {
  id: number;
  email: string;
  firstName: string;
  lastName: string;
  role: string;
}

interface Product {
  id: number;
  code: string;
  name: string;
  description: string;
  category: string;
  price: number;
  current_stock: number;
  size: string;
  color: string;
  gender: string;
}

interface InventoryMovement {
  id: number;
  product_id: number;
  product_name: string;
  product_code: string;
  user_name: string;
  movement_type: 'ENTRADA' | 'SALIDA' | 'AJUSTE' | 'DEVOLUCION';
  quantity: number;
  previous_stock: number;
  new_stock: number;
  reason: string;
  created_at: string;
}

export default function Dashboard() {
  const router = useRouter();
  const [user, setUser] = useState<User | null>(null);
  const [products, setProducts] = useState<Product[]>([]);
  const [movements, setMovements] = useState<InventoryMovement[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingMovements, setLoadingMovements] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [movementSearchTerm, setMovementSearchTerm] = useState('');
  const [showAddProduct, setShowAddProduct] = useState(false);
  const [editingProduct, setEditingProduct] = useState<Product | null>(null);

  useEffect(() => {
    const userStr = localStorage.getItem('user');
    if (!userStr) {
      router.push('/');
      return;
    }
    
    const userData = JSON.parse(userStr);
    setUser(userData);
    fetchDataInParallel();
  }, [router]);

  async function fetchDataInParallel() {
    try {
      const token = localStorage.getItem('sessionToken');
      const [productsResponse, movementsResponse] = await Promise.all([
        fetch('/api/products?limit=50'),
        fetch('/api/inventory-movements?limit=50', {
          headers: { 'Authorization': `Bearer ${token}` }
        })
      ]);

      const [productsData, movementsData] = await Promise.all([
        productsResponse.json(),
        movementsResponse.json()
      ]);

      if (productsData.success) setProducts(productsData.products);
      if (movementsData.success) setMovements(movementsData.movements);

    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
      setLoadingMovements(false);
    }
  }

  async function handleDeleteProduct(id: number) {
    if (!window.confirm('¿Estás seguro que deseas eliminar este producto?')) return;

    try {
      const token = localStorage.getItem('sessionToken');
      const response = await fetch(`/api/products/${id}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`,
          'X-User-Role': user?.role || ''
        }
      });

      const data = await response.json();
      if (response.ok) {
        setProducts(products.filter(p => p.id !== id));
        alert('Producto eliminado exitosamente');
      } else {
        alert(`Error: ${data.message}`);
      }

    } catch (error) {
      console.error('Error deleting product:', error);
      alert('Error al eliminar el producto');
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('sessionToken');
    localStorage.removeItem('user');
    router.push('/');
  };

  const filteredProducts = products.filter(p =>
    p.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    p.code.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const filteredMovements = movements.filter(m =>
    m.product_name.toLowerCase().includes(movementSearchTerm.toLowerCase()) ||
    m.product_code.toLowerCase().includes(movementSearchTerm.toLowerCase()) ||
    m.movement_type.toLowerCase().includes(movementSearchTerm.toLowerCase())
  );

  const canManageProducts = ['Administrator', 'Manager', 'ADM_INV'].includes(user?.role || '');
  const canDeleteProducts = ['Administrator'].includes(user?.role || '');
  const canEditProducts = ['Administrator', 'Manager'].includes(user?.role || '');
  const canManageUsers = ['Manager'].includes(user?.role || '');

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('es-PE', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  return (
    <div className="min-h-screen bg-background">
      
      {/* HEADER */}
      <header className="bg-card border-b border-border shadow-sm sticky top-0 z-10">
        <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Package className="w-8 h-8 text-primary" />
            <div>
              <h1 className="text-2xl font-bold text-foreground">YeniJeans</h1>
              <p className="text-xs text-muted-foreground">Control de Inventario</p>
            </div>
          </div>

          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="text-sm font-medium text-foreground">
                {user?.firstName} {user?.lastName}
              </p>
              <p className="text-xs text-primary font-semibold">{user?.role}</p>
            </div>

            <Button onClick={handleLogout} variant="outline" size="sm" className="gap-2">
              <LogOut className="w-4 h-4" />
              Cerrar Sesión
            </Button>
          </div>
        </div>
      </header>

      {/* MAIN */}
      <main className="max-w-7xl mx-auto px-6 py-8">
        
        {/* TABS */}
        <Tabs defaultValue="products" className="w-full">

          <TabsList
            className={`grid w-full ${
              canManageUsers ? 'grid-cols-5' : 'grid-cols-4'
            } gap-4 mb-8`}
          >
            <TabsTrigger value="products" className="flex items-center gap-2">
              <Package className="w-4 h-4" />
              Productos
            </TabsTrigger>

            <TabsTrigger value="movements" className="flex items-center gap-2">
              <History className="w-4 h-4" />
              Movimientos
            </TabsTrigger>

            <TabsTrigger value="analytics" className="flex items-center gap-2">
              <BarChart3 className="w-4 h-4" />
              Analíticas
            </TabsTrigger>

            <TabsTrigger value="suppliers" className="flex items-center gap-2">
              <Truck className="w-4 h-4" />
              Proveedores
            </TabsTrigger>

            {canManageUsers && (
              <TabsTrigger value="users" className="flex items-center gap-2">
                <Users className="w-4 h-4" />
                Usuarios
              </TabsTrigger>
            )}
          </TabsList>

          {/* TAB: PRODUCTS */}
          <TabsContent value="products">
            {/* ... TU CÓDIGO ORIGINAL DE PRODUCTOS SIN CAMBIOS ... */}
            {/* ↓↓↓ (no lo repito para no duplicar 700 líneas aquí) */}
          </TabsContent>

          {/* TAB: MOVEMENTS */}
          <TabsContent value="movements">
            {/* ... TU CÓDIGO ORIGINAL DE MOVIMIENTOS SIN CAMBIOS ... */}
          </TabsContent>

          {/* TAB: ANALYTICS (NUEVA) */}
          <TabsContent value="analytics" className="space-y-8">
            <h2 className="text-3xl font-bold mb-4">Analíticas</h2>
            <AnalyticsDashboard />
          </TabsContent>

          {/* TAB: SUPPLIERS (NUEVA) */}
          <TabsContent value="suppliers" className="space-y-8">
            <h2 className="text-3xl font-bold mb-4">Proveedores</h2>
            <SuppliersManagement />
          </TabsContent>

          {/* TAB: USERS */}
          {canManageUsers && (
            <TabsContent value="users">
              <UserManagementTab />
            </TabsContent>
          )}
        </Tabs>
      </main>
    </div>
  );
}
